import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  name: "BasicInfoForm",
  components: {
    Treeselect: Treeselect
  },
  props: {
    info: {
      type: Object,
      default: null
    },
    menus: {
      type: Array,
      default: []
    }
  },
  data: function data() {
    return {
      rules: {
        tplCategory: [{
          required: true,
          message: "请选择生成模板",
          trigger: "blur"
        }],
        packageName: [{
          required: true,
          message: "请输入生成包路径",
          trigger: "blur"
        }],
        moduleName: [{
          required: true,
          message: "请输入生成模块名",
          trigger: "blur"
        }],
        businessName: [{
          required: true,
          message: "请输入生成业务名",
          trigger: "blur"
        }],
        functionName: [{
          required: true,
          message: "请输入生成功能名",
          trigger: "blur"
        }]
      }
    };
  },
  created: function created() {},
  methods: {
    /** 转换菜单数据结构 */
    normalizer: function normalizer(node) {
      if (node.children && !node.children.length) {
        delete node.children;
      }

      return {
        id: node.menuId,
        label: node.menuName,
        children: node.children
      };
    }
  }
};